import React from "react";
import s from "./Profile.module.css"
import Loader from "../../common/loader/loader";
import ProfileState from "./ProfileState.js"

const Profile=({profile,editEnable,status, updateProfileStatus})=>{
    if(profile){
        return  <div className={s.wrapper}>
            <div>
                <div>
                    {
                        (profile.photos && profile.photos.small)?
                            <img className={s.caption} src={profile.photos.small}/>:
                            <img className={s.caption} src={"data:image/jpeg;base64,"}/>

                    }

                </div>
                <div>
                    <ul>
                        {

                            (profile.contacts)?
                                Object.entries(profile.contacts).map(([key,value])=>{
                                    if(value!==null){
                                        return <li><span>{key}</span>: <span>{value}</span></li>
                                    }
                                })
                                :""
                        }
                    </ul>
                </div>
            </div>
            <div>
                <h2>{profile.fullName}<br/><small>{profile.aboutMe}</small></h2>

                {
                    profile.lookingForAJob ?
                        <div>
                            <div>ИЩУ Работу !!!</div>
                            <div><small>{profile.lookingForAJobDescription}</small></div>
                        </div>:""
                }

            </div>
            <div>
                <ProfileState editEnable={editEnable} status={status} updateProfileStatus={updateProfileStatus}/>
            </div>
        </div>
    }
    return <div><Loader/></div>

}

export default Profile;